import { NgModule } from '@angular/core';
import { Routes, Route, RouterModule } from '@angular/router';
import { LayoutComponent } from './layout/layout.component';
import * as camelcase from 'camelcase';

import { plugins, defaultPluginPath } from '../config';

const routesPlugins: Routes = [
  {
    path: 'login',
    loadChildren: () =>
      import('../loginscreen/loginscreen.module')
        .then(mod => mod.LoginscreenModule),
  },
  {
    path: '', pathMatch: 'full', redirectTo: defaultPluginPath,
  },
];

const routes: Routes = [
  {
    path: '', component: LayoutComponent,
    children: routesPlugins,
  }
];

plugins.forEach((data) => {
  const { name, path }  = data;
  routesPlugins.push({
    path: path.toLowerCase(),
    loadChildren: () => {
      return import(`../plugins/${name}/${name}.module`)
       .then(m => m[camelcase(name, { pascalCase: true }) + 'Module'])
    },
  });
});

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CoreRoutingModule { }
